	module.exports = {"data":{"markdownRemark":{"html":"<br/>\n<p align=\"center\">\n  <img style='width:80%;' border=\"0\" alt=\"testo sopra\" src=\"/media/photos/dolomiti-yule.jpg\">\n</p>\n<br/>\n<span align=\"center\" style=\"color:#3D047A\">\n<p><em>Io sono l’aria<br>\nche gelida gioca tra le cime innevate,<br>\nsono il vento impetuoso<br>\nche strappa dai rami le ultime foglie,<br>\nsono la brezza gentile<br>\nche fa fremere le fiamme nei camini.<br>\nIo sono il minuscolo scricciolo e il possente gipeto<br>\nsono passero, merlo e falco<br>\npettirosso, corvo e aquila,<br>\nsono ogni uccello del cielo<br>\nche sul mio spirito spiega le ali<br>\naccarezzandolo con seriche piume<br>\ne trovando in esso sostegno nel volo.<br>\nIo sono la quiete prima del risveglio<br>\nil riposo delle membra nel silenzio della morte,<br>\ncon il mio soffio spazzo il superfluo<br>\nsciolgo i nodi delle stagioni passate<br>\nsgretolo la carne affinchè rimangano solo ossa,<br>\ncandida essenza dei corpi,<br>\ncosicché possano rifiorire a primavera.<br>\nPrendimi per mano e lasciati cullare<br>\npermettimi di liberarti dai pesi che ti opprimono<br>\nlibrati con me nell’immenso firmamento dei tuoi sogni.</em></p>\n<p>Shar  </p>\n</span>","frontmatter":{"path":"/home","title":""}}},"pathContext":{}}

